<template>
    <div class="login">
        <h3>Sign In</h3>
        <input type="text" v-model="email" placeholder="Email"><br>
        <input type="password" v-model="password" placeholder="Password"><br>
        <button @click="login">Connection</button>
        <p>
            or Sign In with Google <br>
            <button @click="socialLogin" class="social-button">
                <img alt="Google Logo" src="https://raw.githubusercontent.com/CaptainYouz/vue-firebase-tutorial/social-sign-in/src/assets/google-logo.png">
            </button>
        </p>
        <p>You don't have an account ? You can <router-link to="/sign-up">create one</router-link></p>
    </div>
</template>

<script>
    import firebase from 'firebase'
    import {AXIOS} from './http-common'

    const config = {
        apiKey: "AIzaSyB5am9vDj6XghD_atAZN0FKbW-9sZ0azMs",
        authDomain: "spring-boot-vue-1545055785370.firebaseapp.com",
        databaseURL: "https://spring-boot-vue-1545055785370.firebaseio.com",
        projectId: "spring-boot-vue-1545055785370",
        storageBucket: "spring-boot-vue-1545055785370.appspot.com",
        messagingSenderId: "217594607633"
    }

    firebase.initializeApp(config)

    export default {
        name: 'Login',
        data() {
            return {
                email: '',
                password: ''
            }
        },
        methods: {
            login() {
                firebase.auth().signInWithEmailAndPassword(this.email, this.password).then((user) => {
                    this.$router.replace('home');
                    console.log(user)
                }).catch((err) => {
                    alert('Oops. ' + err.message)
                });
            },
            socialLogin() {
                const provider = new firebase.auth.GoogleAuthProvider();

                firebase.auth().signInWithPopup(provider).then((result) => {
                    this.$router.replace('');
                    let param = new URLSearchParams()
                    param.append('uid', result.user.uid)

                    AXIOS.post('/user', param).then((result) => {
                        console.log(result)
                    }).catch((err) => {
                        alert('Oops. ' + err.message)
                    });

                }).catch((err) => {
                    alert('Oops. ' + err.message)
                });
            }
        }
    }
</script>

<style scoped>  /* "scoped" attribute limit the CSS to this component only */
.login {
    margin-top: 40px;
}
input {
    margin: 10px 0;
    width: 20%;
    padding: 15px;
}
button {
    margin-top: 20px;
    width: 10%;
    cursor: pointer;
}
p {
    margin-top: 40px;
    font-size: 13px;
}
p a {
    text-decoration: underline;
    cursor: pointer;
}
.social-button {
    width: 75px;
    background: white;
    padding: 10px;
    border-radius: 100%;
    box-shadow: 0 2px 4px 0 rgba(0,0,0,0.2);
    outline: 0;
    border: 0;
}
.social-button:active {
    box-shadow: 0 2px 4px 0 rgba(0,0,0,0.1);
}
.social-button img {
    width: 100%;
}
</style>